import throttle from 'lodash.throttle';
import { jsonParceWhereStorage } from './module/jsonParceWhereStorage ' // –º–æ–¥—É–ª—å –ø–∞—Ä—Å–∞ JSON –∏–ª localStorage
import { objData } from './module/objData' // –û–±—ä–µ–∫—Ç —Ö—Ä–∞–Ω–µ–Ω–∏—è –¥–∞–Ω–Ω—ã—Ö –∏–∑ —Ñ–æ—Ä–º—ã

const form = document.querySelector('.feedback-form')
const emailInput = document.querySelector('.feedback-form input')
const textAreaInput = document.querySelector('.feedback-form textarea')

form.addEventListener('submit', onsubForm)
emailInput.addEventListener('input', throttle(onGetObj, 500))
textAreaInput.addEventListener('input', throttle(onGetObj, 500))

const statusStorage = jsonParceWhereStorage("feedback-form-state") // –ó–∞–ø–∏—Å—ã–≤–∞–µ—Ç —Ä–µ–∑—É–ª—å—Ç–∞—Ç –ø–∞—Ä—Å–∞ JSON –≤ –ø–µ—Ä–µ–º–µ–Ω–Ω—É—é

if(statusStorage){                                                 // –ï—Å–ª–∏ true, —Ç–æ –ø–æ–∫–∞–∑—ã–≤–∞–µ–º –≤ –ø–æ–ª—è—Ö –¥–∞–Ω–Ω—ã–µ –∏–∑ localStorage
    emailInput.value = statusStorage.email;
    textAreaInput.value = statusStorage.message;
}

function onsubForm(event){                                         // –º–µ—Ç–æ–¥ –ø—Ä–∏ submit`e 
    event.preventDefault()
    event.target.reset()
    localStorage.removeItem("feedback-form-state")
    console.log(objData)
}

function onGetObj(event){                                          // –î–æ–±–∞–≤–ª—è–µ—Ç—Å—è –≤ –æ–±—ä–µ–∫—Ç –∫–ª—é—á –∏ –Ω–æ–≤–æ–µ –∑–Ω–∞—á–µ–Ω–∏–µ 
    objData[event.target.name] = event.target.value
    objData[event.target.name] = event.target.value
    localStorage.setItem("feedback-form-state", JSON.stringify(objData))
 
}



